<template>
  <div>
    <table>
      <colgroup>
        <col style="width: 100px;">
        <col style="width: 100px;">
        <col style="width: 120px;">
        <col>
      </colgroup>

      <tbody>
        <tr>
          <td colspan="4">
            <div class="image">
              <canvas id="viewCanvas"></canvas>
            </div>
          </td>
        </tr>
        
        <tr>
          <th>지역 : </th>
          <td>
            <select @change="fn_setImage">
              <option key="default" value="default">기본</option>
              <option key="ng" value="ng">경기 북부</option>
              <option key="se" value="se">서울</option>
            </select>
          </td>
          <th>넣을 내용 : </th>
          <td>
            <input type="text" @input="fn_setText">
          </td>
        </tr>
      </tbody>
    </table>
  </div>

  <canvas id="resultCanvas"></canvas>

  <div class="btn-wrap">
    <!-- <button @click="fn_download()">다운로드</button> -->
    <button @click="fn_download()">다운로드</button>
  </div>
</template>

<script>
import CanvasJS from '@/js/canvas.js';
import options from '@/json/sizeValues.json';

export default {
  name: 'CoverComponent',
  props: {},
  data() {
    return {
      imgCanvas   : '',
      resultCanvas: '',
      images      : {
        default: require("@/assets/template/template_2.png"),
        ng: require("@/assets/template/template_ng.png"),
        se: require("@/assets/template/template_se.png")
      }
    };
  },
  created() {
    var keys = Object.keys(options);
    for (let i = 0; i < keys.length; i++) {
      options[keys[i]].img = this.images[keys[i]];
    }
  },
  mounted() {
    this.imgCanvas = CanvasJS.init('resultCanvas', options['default']);
    this.imgCanvas.fn_loadImage(this.imgCanvas, options['default'].img);
  },
  methods: {
    fn_setText(_val) {
      if (_val.target.value.length >= 13) {
          alert("12자까지 입력 가능합니다.");

          _val.target.value = _val.target.value.substring(0,(_val.target.value.length - 1));
          return;
      }

      this.imgCanvas.fn_fillText(this.imgCanvas, _val.target.value);
    },

    fn_setImage(_val) {
      console.log(_val.target.value);
      this.imgCanvas = CanvasJS.init('resultCanvas', options[_val.target.value]);
      this.imgCanvas.fn_loadImage(this.imgCanvas, options[_val.target.value].img);
    },

    fn_download() {
      const a = document.createElement('a');
      a.href = this.imgCanvas.canvas.toDataURL();
      a.download = 'result.png';
      a.click();
      a.remove();
    }
  }
}
</script>

